.features {
  //font-size: 16px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 10em;
  height: auto;
  //max-width: 100%;
}

// MEDIA
@media (max-width: 1400px) {
  .features {
    padding: 5em 0 7em 0;
  }
}

@media (max-width: 1030px) {
  .features {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
