.about {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 17em;
  width: 100%;

  &__img {
    width: 450px;
    height: 450px;
    background: url("../img/about_Vasily_Mishanin.gif");
    background-size: cover;
    border-radius: 0% 100% 0% 100% / 77% 20% 80% 23%;
    //border-radius: 230px;
  }

  &__content {
    width: 600px / $maxWidthContainer * 100%;
    font-size: 16px;
  }

  &__link {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 1.25em;
    line-height: 2em;
    margin-bottom: 0.5em;

    a {
      color: $colorPrimary;
    }
  }

  &__title {
    font-family: $fontFamSecondary;
    font-weight: 700;
    font-size: 3em;
    line-height: 1.2;
    margin-top: 0;
  }

  &__subtitle {
    font-family: $fontFamPrimary;
    font-size: 1em;
    line-height: 2;
    color: $colorSubtitlePrimary;
    margin-top: 0;
    margin-bottom: 1.5em;
  }
  &__list {
    margin-bottom: 1.5em;
  }

  &__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  &__badge {
  }

  &__text {
    font-family: $fontFamPrimary;
    font-size: 1em;
    line-height: 3;
    font-weight: 400;
    margin-left: 1.2em;
    color: $colorAboutItem;
  }

  &__button {
    width: 182px;
    height: 52px;
    box-shadow: 0px 15px 20px rgba(52, 178, 255, 0.2);
    border-radius: 26px;
    &:hover {
      background-color: $colorWhite;
      border: 2px solid $colorPrimary;

      .button__body {
        color: $colorPrimary;
      }
    }
  }
}

// MEDIA
@media (max-width: 1550px) {
  .about {
    justify-content: space-around;

    &__img {
      width: 250px;
      height: 250px;
    }

    &__content {
      width: 480px;
      font-size: 14px;
    }

    &__subtitle {
      line-height: 1.5;
    }
    &__list {
      margin-bottom: 1.5em;
    }

    &__text {
      line-height: 2.5;
      margin-left: 1em;
    }
  }
}

@media (max-width: 986px) {
  .about {
    flex-direction: column;

    &__img {
      width: 350px;
      height: 350px;
    }

    &__content {
      width: auto;
    }

    &__text {
      line-height: 2.5;
    }
  }
}

@media (max-width: 860px) {
  .about {
    margin-bottom: 4em;
  }
}

@media (max-width: 570px) {
  .about {
    &__img {
      width: 300px;
      height: 300px;
    }

    &__title {
      font-size: 2em;
    }

    &__item {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      margin-bottom: 0.5em;
    }

    &__text {
      line-height: 1.5;
    }
  }
}
