.banner {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 3rem 0 20rem 0;
  background: url("../img/bg-header.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-clip: border-box;

  // Banner HEADER
  &__header {
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 52px;
    width: 100%;
  }

  &__logo {
    font-size: 30px;
    width: 115px;
    height: 39px;

    .logo_img {
      width: 40px;
      height: 38px;
    }
  }

  &__nav {
    width: 492px / $maxWidthContainer * 100%;
    &-list {
      display: flex;
      justify-content: space-between;
    }
    &-item {
    }
    &-link {
    }
  }

  &__button_free {
    width: 148px;
    height: 52px;
    border-radius: 26px;
    &:hover {
      background-color: #fff;

      .button__body {
        color: $colorSecondary;
      }
    }
  }

  // BANNER CONTENT
  &__content {
    font-size: 16px;
    width: 50%;
    align-self: flex-start;
    padding-top: 7em;
  }

  &__title {
    font-size: 3.5em;
    font-weight: 900;
    font-style: normal;
    line-height: 1.25;
    color: $colorWhite;
  }

  &__subtitle {
    font-size: 1em;
    line-height: 2;
    color: $colorSubtitleBanner;
    margin-right: 3em;
    padding-bottom: 1.5em;
  }

  &__actions {
    display: flex;

    &-item {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  //   Banner Buttons
  &__button {
    &_get-started {
      width: 187px;
      height: 61px;
      border-radius: 30px;

      &:hover {
        background-color: #fff;

        .button__body {
          color: $colorSecondary;
        }
      }
    }
  }

  .button-watch-video {
    margin-left: 2em;

    &__link {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .button-play {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 3.8em;
      height: 3.8em;
      border-radius: 1.9em;
      background-color: #fff;

      &__sign {
        width: 0;
        height: 0;
        border-top: 12px solid transparent;
        border-bottom: 12px solid transparent;
        border-left: 17px solid $colorPrimary;
        border-radius: 10px;
        margin-left: 4px;

        &:hover {
          border-left: 17px solid #fff;
        }
      }

      &:hover {
        background-color: $colorPrimary;
      }
    }
    .button__body {
      padding: 0;
      margin-left: 1em;
    }
  }
}

// MEDIA
@media (max-width: 1400px) {
  .banner {
    background-size: cover;

    padding-bottom: 10em;
    // BANNER CONTENT
    &__content {
      font-size: 14px;
      width: 100%;
      padding-top: 5em;
    }
  }
}

@media (max-width: 986px) {
  .banner {
    &__header {
      flex-direction: column;
      align-items: flex-start;
      width: 30%;
      height: 100%;
    }

    &__content {
      width: 65%;
      padding-top: 0;
    }

    &__title {
      font-size: 2.5em;
      margin-top: 0;
    }

    &__nav {
      width: 492px / $maxWidthContainer * 100%;
      &-list {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
      &-item {
        margin-bottom: 0.5em;
      }
      &-link {
        font-size: 1.2em;
      }
    }
  }
}

// MEDIA MOBILE
@media (max-width: 768px) {
  .banner {
    &__header {
      -ms-flex-direction: column;
      align-items: center;
      height: auto;
      width: auto;
    }

    &__nav {
      margin-top: 2em;
      &-item {
        margin-bottom: 1em;
      }
      &-link {
        font-size: 1.5em;
      }
    }

    &__button_free {
      width: 148px;
      height: 52px;
      border-radius: 26px;
    }

    // BANNER CONTENT
    &__content {
      width: 90%;
      align-self: center;
      padding-top: 3em;
    }

    &__title {
      font-size: 2em;
    }

    &__subtitle {
    }

    &__actions {
      display: flex;

      &-item {
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
    //   Banner Buttons
    &__button {
      &_get-started {
        width: 150px;
        height: 50px;
        border-radius: 25px;
      }
    }

    .button-watch-video {
      margin-left: 1em;

      &__link {
      }

      .button-play {
        width: 5em;
        height: 3.8em;

        &__sign {
          width: 0;
          height: 0;
          border-top: 12px solid transparent;
          border-bottom: 12px solid transparent;
          border-left: 17px solid $colorPrimary;
          border-radius: 10px;
          margin-left: 4px;
        }
      }
      .button__body {
        padding: 0;
        margin-left: 1em;
      }
    }
  }
}
