.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../img/bg-footer.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-clip: border-box;
  padding-top: 25em;

  &__content {
    display: flex;
    justify-content: space-between;
    padding-bottom: 5em;
  }

  &__company {
    width: 28%;
    &-info {
      font-family: $fontFamPrimary;
      font-size: 1em;
      line-height: 2;
      color: $colorFooterInfo;
    }
  }

  &__logo {
    font-size: 36px;
    width: 133px;
    height: 42px;

    .logo_img {
      width: 40px;
      height: 48px;
    }
  }

  &__social {
    // img:first-child {
    //   margin-left: -30px;
    // }

    // img + img {
    //   margin-left: -45px;
    // }
  }

  &__links {
    &-title {
      margin-top: 0.3em;
      font-family: $fontFamSecondary;
      font-style: normal;
      font-weight: 600;
      font-size: 1.5em;
      line-height: 30px;
      color: $colorWhite;
    }
  }

  &__nav {
    &-list {
      margin-top: 2em;
    }

    &-item {
      font-family: $fontFamPrimary;
      font-size: 1em;
      line-height: 2.5;
    }
  }

  &__copyright {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__rights,
  &__policy {
    font-family: $fontFamPrimary;
    font-weight: 300;
    font-size: 1rem;
    line-height: 2;
    color: $colorWhite;
  }

  &__policy {
  }
}

// MEDIA
@media (max-width: 1400px) {
  .footer {
    padding-top: 16em;

    &__content {
      padding-bottom: 1em;
    }

    &__company {
      &-info {
        line-height: 1.25;
      }
    }

    &__links {
      &-title {
        font-size: 1.2em;
      }
    }

    &__rights,
    &__policy {
      line-height: 1;
    }
  }
}

@media (max-width: 1000px) {
  .footer {
    background-size: cover;

    &__content {
      font-size: 10px;
    }

    &__nav {
      &-item {
        a {
          font-size: 14px;
        }
      }
    }

    &__rights,
    &__policy {
      font-size: 14px;
    }
  }
}

@media (max-width: 860px) {
  .footer {
    padding-top: 0;
    background-size: cover;

    &__content {
      flex-direction: column;
      align-items: center;
      padding-top: 3em;
    }

    &__content {
      font-size: 18px;
      padding-top: 26em;
    }

    &__company {
      padding-top: 10em;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;

      &-info {
        width: 200px;
      }
    }

    &__social {
      display: flex;
      flex-direction: column;
      align-items: center;
      img:first-child {
        margin-left: 0;
      }

      img + img {
        margin-left: 0;
      }
    }

    &__links {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 2em;
    }

    &__about {
      margin-bottom: 2em;
    }

    &__contact {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 2em;
    }

    &__nav {
      &-list {
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      &-item {
        text-align: center;
        a {
          font-size: 16px;
        }
      }
    }

    &__copyright {
      flex-direction: column-reverse;
      align-items: center;
      padding-top: 1em;
    }
  }
}
