.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 4em 2.5em;
  background: $colorWhite;
  box-shadow: 0px 10px 26px rgba(0, 0, 0, 0.04);
  border-radius: 5px;
  width: 370px / $maxWidthContainer * 100%;

  &:hover {
    box-shadow: 0 4px 1px 0px $colorSubtitlePrimary;
    cursor: pointer;
  }

  &_ML {
    box-shadow: 0px 10px 30px rgba(52, 178, 255, 0.16);
  }

  &__img {
  }
  &__title {
    margin-top: 0;
    color: $colorCardTitle;
  }
  &__subtitle {
    line-height: 2em;
  }
  &__button {
    width: 163px;
    height: 50px;
    border-radius: 25px;

    &:hover {
      background-color: $colorWhite;
      border: 2px solid $colorPrimary;

      .button__body {
        color: $colorPrimary;
      }
    }

    &_light {
      border: 2px solid $colorPrimary;
      background-color: $colorWhite;

      .button__body {
        color: $colorPrimary;
      }

      &:hover {
        background-color: $colorPrimary;

        .button__body {
          color: $colorWhite;
        }
      }
    }
  }
}

// MEDIA
@media (max-width: 1400px) {
  .card {
    padding: 1.5em 1em;
    min-width: 190px;

    &__subtitle {
      line-height: 1.5em;
    }
  }
}

@media (max-width: 1030px) {
  .card {
    margin-bottom: 2em;
    width: 450px;
  }
}

@media (max-width: 600px) {
  .card {
    width: 300px;
  }
}
