.signup {
  position: absolute;
  width: 100%;
  bottom: -240px;
  border-radius: 10px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.08);

  &__inner {
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    padding: 4em;
    background-color: $colorSignUp;
    border-radius: 10px;
    box-shadow: 0 0 0 1em $colorWhite;
  }

  &__info {
    width: 40%;
  }

  &__title {
    font-family: $fontFamSecondary;
    font-weight: 700;
    font-size: 2.25em;
    color: $colorCardTitle;
  }

  &__subtitle {
    font-family: $fontFamPrimary;
    font-size: 1em;
    line-height: 2;
    font-weight: 400;
  }

  &__form {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 60%;
  }

  &__input {
    width: 21em;
    height: 4.75em;
    border: none;
    background: $colorWhite;
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.04);
    border-radius: 5px 0px 0px 5px;

    &:hover {
      box-shadow: 0px 0px 7px 1px #777;
    }

    &::placeholder {
      padding-left: 2em;
      font-family: $fontFamPrimary;
      font-weight: 300;
      font-size: 1em;
      line-height: 2;
      color: #777;
    }
  }

  &__button {
    width: 5em;
    height: 4.75em;
    background: $colorPrimary;
    border: none;
    border-radius: 0px 5px 5px 0px;
    cursor: pointer;

    &:hover {
      box-shadow: 0px 0px 7px 1px #777;
    }
  }
}

// MEDIA
@media (max-width: 1400px) {
  .signup {
    bottom: -190px;

    &__inner {
      padding: 1em 2em;
    }

    &__info {
      width: 45%;
    }

    &__subtitle {
      font-family: $fontFamPrimary;
      font-size: 1em;
      line-height: 2;
      font-weight: 400;
    }

    &__subtitle {
    }

    &__form {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      width: 60%;
    }
    &__input {
      width: 17em;
      height: 3.5em;
    }

    &__button {
      width: 4em;
      height: 3.5em;
    }
  }
}

@media (max-width: 900px) {
  .signup {
    &__inner {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }

    &__info {
      width: auto;
    }

    &__form {
      width: auto;
    }

    &__input {
      width: 14em;
      height: 3.5em;
    }

    &__button {
      width: 4em;
      height: 3.5em;
    }
  }
}

@media (max-width: 860px) {
  .signup {
    bottom: -350px;
  }
}
