.top-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 3em;
}

// MEDIA
@media (max-width: 986px) {
  .top-container {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    padding-bottom: 3em;
  }
}

@media (max-width: 768px) {
  .top-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 3em;
  }
}
